.ma-flex-container, .ma-header-information, .ma-menu-navigation, .ma-widgets, .ma-widget-info, .ma-widget-menu, .ma-widget-statistic {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
}

.ma-widget-info-text, .ma-widget-info__incoming, .ma-widget-info__outgoing, .ma-widget-info__missed, .ma-widget-info__all {
  color: #8a8a8a;
  font-size: 13px;
  font-family: "PTSansRegular", sans-serif;
  font-weight: 400;
}

.ma-header-information {
  flex-wrap: wrap;
  justify-content: space-around;
}

.ma-header-information__item {
  list-style: none;
  padding-top: 10px;
}

.ma-header-information__user {
  color: #50567a;
  font-family: "PTSansBold", sans-serif;
  font-size: 18px;
}

.ma-header-information__user:after {
  content: '|';
  color: #deeaea;
  margin-left: 5px;
}

.ma-header-information__btn {
  color: #50567a;
  font-family: "PTSansBold", sans-serif;
  font-size: 18px;
  text-decoration: underline;
  margin-left: 10px;
}

.ma-header-information__btn:hover {
  color: #2aabd2;
}

.ma-menu {
  background: #4b6293;
  padding: 15px 0 14px;
}

.ma-menu-navigation {
  justify-content: flex-start;
  position: relative;
}

.ma-menu-item {
  list-style: none;
}

.ma-menu-item--right {
  position: absolute;
  right: 0;
}

.ma-menu-item__content {
  color: #ffffff;
  font-family: "PTSansRegular", sans-serif;
  font-size: 18px;
}

.ma-menu-item__content:hover, .ma-menu-item__content:visited, .ma-menu-item__content:link {
  color: #ffffff;
  text-decoration: none;
}

.ma-menu-item__content:hover {
  color: #7e98cb;
}

.ma-menu-item__widget {
  color: #7e98cb;
  margin: auto 20px;
}

.ma-menu-item__widget:hover, .ma-menu-item__widget:visited, .ma-menu-item__widget:link {
  color: #7e98cb;
}

.ma-menu-item__operators {
  margin: auto 20px;
}

.ma-menu-item__statistic {
  position: relative;
  padding-left: 20px;
}

.ma-menu-item__statistic:before {
  content: url("../../assets/img/head-statistic-icon.png");
  position: absolute;
  top: 2px;
  left: -5px;
}

.ma-menu-item__btn {
  float: right;
  margin-top: -4px;
  margin-right: 13px;
  padding: 7px 25px 4px;
  border-radius: 2px;
  color: #000;
  font-family: "PTSansRegular", sans-serif;
  font-size: 14px;
  text-transform: uppercase;
  background: #f79621;
  /* Old browsers */
  background: -moz-linear-gradient(left, #f79621 1%, #f9c667 100%);
  /* FF3.6-15 */
  background: -webkit-linear-gradient(left, #f79621 1%, #f9c667 100%);
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to right, #f79621 1%, #f9c667 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
}

.ma-menu-item__btn:hover, .ma-menu-item__btn:visited, .ma-menu-item__btn:link {
  text-decoration: none;
}

.ma-menu-item__btn:hover {
  box-shadow: 2px 2px 2px rgba(255, 255, 255, 0.8);
}

.ma-menu-item__information {
  float: right;
  padding-right: 20px;
  font-size: 16px;
}

.ma-menu-item__text {
  color: #7e98cb;
  font-size: 18px;
}

